.container{
    height: 100%;
    min-height: 100vh;
    width: 100%;
    background: linear-gradient(to right, #9900ff, #cc80ff);
    display: flex;
    justify-content: center;
    align-items: center;
}

.innerBox{
    min-width: 400px;
    height: 100%;
    width: fit-content;
    background-color: #fff;
    box-shadow: 1px 1px 4px rgba(0,0,0,0.2);
    padding: 30px;
    border-radius:  10px;
    display: flex;
    flex-direction: column;
    gap: 30px;
}

.footer{
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.footer .error{
    font-weight: bold;
    font-size: 0.85rem;
    color: #ff0026dd;
    text-align: center;
}

.footer button{
    outline: none;
    border: none;
    background-color: #9900ff;
    color: #fff;
    border-radius: 5px ;
    font-weight: bold;
    font-size: 1rem;
    padding: 10px 16px;
    width: 100%;
    transition: 100%;
    cursor: pointer;

}

.footer button:disabled{
    background-color: grey !important;
}

.footer button:hover{
    background-color: #aa2aff;

}

.footer p{
    font-weight: 700;
    color: #000;
}

.footer p span a{
    font-weight: bold;
    color: #9900ff;
    letter-spacing: 1px;
    font-size: 1rem;
    text-decoration: none;
}